"use client";
import { useSession } from "next-auth/react";
import SignInButton from "./components/SignInButton";
import { useRouter } from "next/router";

export default function Home() {
  const { data: session } = useSession();
  const router = useRouter();

  if (session) {
    router.push("/dashboard"); // redirect logged-in user to dashboard
    return <p>Redirecting...</p>;
  }

  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        height: "100vh",
        backgroundColor: "#f5f5f5",
        fontFamily: "Arial, sans-serif",
      }}
    >
      <div
        style={{
          backgroundColor: "#fff",
          padding: "60px 40px",
          borderRadius: "8px",
          boxShadow: "0 4px 20px rgba(0,0,0,0.1)",
          textAlign: "center",
          maxWidth: "400px",
          width: "100%",
        }}
      >
        {/* Logo or App Name */}
        <h1 style={{ marginBottom: "40px", color: "#202124", fontWeight: 500 }}>
          Tinitiate
        </h1>

        {/* Welcome Text */}
        <p style={{ color: "#5f6368", marginBottom: "30px" }}>
          Sign in with Google to continue
        </p>

        {/* Sign In Button */}
        <SignInButton />

        {/* Footer / Small Text */}
        <p style={{ marginTop: "30px", fontSize: "12px", color: "#9aa0a6" }}>
          Your information is safe. We do not share your credentials.
        </p>
      </div>
    </div>
  );
}
